<template>
  <div id="fireControl">
    <div id="myheader">
      <div class="container-fluid" style="padding:0px;height:100%;width:100%;">
        <div class="row" style="margin:0px;height:100%;width:100%;">
          <div class="col-xs-2" style="padding:0px;height:100%;"><img src="../assets/logo.png" alt="logo" height="90%"></div>
          <div class="col-xs-8" style="padding:0px;height:100%;"><div class="text-center" id="text"></div></div>
        </div>
      </div>
    </div>
    <div id="main">
      <div id="menu">
        <div id="icons">
            <input class="icon" type="checkbox" v-model="firemenStatus"><img src="../assets/fireman.png" alt="firemen"><span></span>
            <input class="icon" type="checkbox" v-model="carStatus"><img src="../assets/car.png" alt="car"><span></span>
            <input class="icon" type="checkbox" v-model="hydrantStatus"><img src="../assets/hydrant.png" alt="hydrant"><span></span>
            <input class="icon" type="checkbox" v-model="smokeStatus"><img src="../assets/smoke.png" alt="smoke"><span></span>
            <input class="icon" type="checkbox" v-model="wsdStatus"><img src="../assets/wsd2.png" alt="wsd"><span></span>
            <input class="icon" type="checkbox" v-model="stationStatus"><img src="../assets/station.png" alt="station"><span></span>     
            <input class="icon" type="checkbox" v-model="miniStatus"><img src="../assets/mini.png" alt="mini"><span></span>  
            <input class="icon" type="checkbox" v-model="roomStatus"><img src="../assets/room.png" alt="room"><span></span> 
            <span id="data" @click="showData()"><Icon type="ios-paper" size="20" /><span style="margin:0px 3px;"></span> </span>
        </div>     
      </div>
      <div id="map">
        <div id="mapshow"></div>
      </div>
    </div>
    <!--  -->
    <div class="videoShow" id="firemen" v-if="firemenVideo">
      <div class="videoHide"><div class="move" @mousedown="move"></div><span class="glyphicon glyphicon-remove" aria-hidden="true" @click="firemenVideo=false;"></span></div>
      <iframe class="iframe" src="about:blank" frameBorder="0" marginHeight="0" marginWidth="0" style="position:absolute; visibility:inherit; top:0px;left:0px;z-index:-1;"></iframe>
      <div class="msg" v-html="firemenMsg"></div>
    </div>
    <!--  -->
    <div class="videoShow" id="car" v-if="carVideo">
      <div class="videoHide"><div class="move" @mousedown="move"></div><span class="glyphicon glyphicon-remove" aria-hidden="true" @click="carVideo=false;"></span></div>
      <iframe class="iframe" src="about:blank" frameBorder="0" marginHeight="0" marginWidth="0" style="position:absolute; visibility:inherit; top:0px;left:0px;z-index:-1;"></iframe>
      <div class="msg" v-html="carMsg"></div>
    </div>
    <!--  -->
    <div class="videoShow" id="station" v-if="stationVideo">
      <div class="videoHide"><div class="move" @mousedown="move"></div><span class="glyphicon glyphicon-remove" aria-hidden="true" @click="stationVideo=false;"></span></div>
      <iframe class="iframe" src="about:blank" frameBorder="0" marginHeight="0" marginWidth="0" style="position:absolute; visibility:inherit; top:0px;left:0px;z-index:-1;"></iframe>
      <div class="msg" v-html="stationMsg"></div>
    </div>
    <!--  -->
    <div class="videoShow" id="picture" v-if="picture">
      <div class="videoHide"><div class="move" @mousedown="move"></div><span class="glyphicon glyphicon-remove" aria-hidden="true" @click="picture=false;"></span></div>
      <iframe class="iframe" src="about:blank" frameBorder="0" marginHeight="0" marginWidth="0" style="position:absolute; visibility:inherit; top:0px;left:0px;z-index:-1;"></iframe>
      <div class="msg" v-html="pictureMsg"></div>
    </div>
    <!--  -->
    <div id="allData" v-if="dataShow">
      <div class="fireCell"><div style="color:rgb(2, 252, 196);">2</div><div></div></div>
      <div class="fireCell"><div style="color:rgb(2, 252, 196);">2</div><div></div></div>
      <div class="fireCell"><div style="color:rgb(2, 252, 196);">1</div><div></div></div>
      <div class="fireCell"><div style="color:rgb(2, 252, 196);">4</div><div></div></div>
      <div class="fireCell" style="border:none;"><div style="color:rgb(2, 252, 196);">2</div><div></div></div>
    </div>
    <!--  -->
    <div class="title1" v-if="dataShow"></div>
    <div id="dataWindow" v-if="dataShow">
      <div class="row">
        <div class="col-xs-6 text-right"></div>
        <div class="col-xs-6 text-left" style="color:#d9534f;">2</div>
      </div>
      <div class="row">
        <div class="col-xs-6 text-right"></div>
        <div class="col-xs-6 text-left" style="color:#02fcc4;">3</div>
      </div>
      <div class="row">
        <div class="col-xs-6 text-right"></div>
        <div class="col-xs-6 text-left" style="color:#02fcc4;">85</div>
      </div>
      <div class="row">
        <div class="col-xs-6 text-right"></div>
        <div class="col-xs-6 text-left">
          <div class="progress">
            <div class="progress-bar" role="progressbar" aria-valuenow="93" aria-valuemin="0" aria-valuemax="100" style="width: 93%;">
              93%
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-6 text-right"></div>
        <div class="col-xs-6 text-left">
          <div class="progress">
            <div class="progress-bar progress-bar-warning" role="progressbar" aria-valuenow="100" aria-valuemin="0" aria-valuemax="100" style="width: 100%;">
              100%
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-6 text-right"></div>
        <div class="col-xs-6 text-left">
          <div class="progress">
            <div class="progress-bar progress-bar-danger" role="progressbar" aria-valuenow="99" aria-valuemin="0" aria-valuemax="100" style="width: 99%;">
              99%
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="title2" v-if="dataShow"></div>
    <div id="alarm" v-if="dataShow">
      <!-- <div id="tableHead"><div class="cell"></div><div class="cell"></div><div class="cell"></div><div class="cell"></div><div class="cell"></div><div class="cell"></div></div>
      <div id="tableBody">
        <div class="cells" v-for="msg in message" :key="msg.id"><div class="cell">{{msg.unit}}</div><div class="cell">{{msg.name}}</div><div class="cell">{{msg.time}}</div><div class="cell">{{msg.type}}</div><div class="cell">{{msg.result}}</div><div class="cell"><Icon type="md-eye" size="18" @click="showAlarm(msg.src)" /><Icon type="md-videocam" size="18" @click="showAlarm(msg.src)" /></div></div>
      </div> -->
      <div id="tableHead"><div class="cell"></div><div class="cell"></div><div class="cell"></div><div class="cell"></div><div class="cell"></div><div class="cell"></div></div>
      <div id="tableBody">
        <div class="cells" v-for="msg in alarmMessage" :key="msg.id"><div class="cell">{{msg.deviceType}}</div><div class="cell">{{msg.areaName}}</div><div class="cell">{{msg.alarmBizName}}</div><div class="cell">{{msg.startTime}}</div><div class="cell">{{msg.handle_status}}</div><div class="cell"><Icon type="md-eye" size="18" /><Icon type="md-videocam" size="18" /></div></div>
      </div>
    </div>
    <!--  -->
    <div class="videoShow" id="alarmPicture" v-if="alarmStatus">
      <div class="videoHide"><div class="move" @mousedown="move"></div><span class="glyphicon glyphicon-remove" aria-hidden="true" @click="alarmStatus=false;"></span></div>
      <iframe class="iframe" src="about:blank" frameBorder="0" marginHeight="0" marginWidth="0" style="position:absolute; visibility:inherit; top:0px;left:0px;z-index:-1;"></iframe>
      <div class="msg" v-html="alarmMsg"></div>
    </div>
    <!--  -->
    <div class="warn">
      <Alert type="warning" show-icon v-for="msg in alarms" :key="msg.id">
        &#160;&#160;&#160;&#160;&#160;&#160;&#160;&#160;{{msg.startTime}}  <Icon type="md-close" @click="deleteThis(msg.id)" style="float:right"/>
        <template slot="desc">
          ID{{msg.deviceID}}{{msg.deviceType}} <span style="color:red">{{msg.alarmBizName}}</span> 
        </template>
      </Alert>
    </div>
  </div>
</template>

<script>
import {styleJson} from '../js/mapstyle'
export default {
  name: 'fireControl',
