        // {name:"",deviceNum:"LGW1_002",num:"0111",img:require("../assets/firemen/3.png"),lng:113.381647,lat:22.796215,rtsp:"rtsp://root:root@238o86j398.qicp.vip:50282/cam0_0",video:require("../assets/videos/2.mp4")}
        {name:"",deviceNum:"LGW1_001",num:"0110",img:require("../assets/firemen/2.png"),lng:113.484324,lat:23.187536,rtsp:"rtsp://238o86j398.qicp.vip:32943/live/av0",video:require("../assets/videos/1.mp4")},
        {name:"",deviceNum:"LGW1_002",num:"0111",img:require("../assets/firemen/3.png"),lng:113.494744,lat:23.173051,rtsp:"rtsp://root:root@238o86j398.qicp.vip:50282/cam0_0",video:require("../assets/videos/2.mp4")}
      ],     
      firemenMarkers:[],
      firemenVideo:false,
      firemenMsg:"",
      //
      carStatus:false,
      cars:[
        // {licenseNum:"A5263",deviceNum:"LGW2_001",num:"0210",img:require("../assets/cars/car1.png"),lng:113.41801,lat:22.793083,rtsp:"rtsp://238o86j398.qicp.vip:32943/live/av0",video:require("../assets/videos/1.mp4")},
        // {licenseNum:"A8456",deviceNum:"LGW2_002",num:"0211",img:require("../assets/cars/car1.png"),lng:113.389911,lat:22.821933,rtsp:"rtsp://root:root@238o86j398.qicp.vip:50282/cam0_0",video:require("../assets/videos/2.mp4")}
        {licenseNum:"A5263",deviceNum:"LGW2_001",num:"0210",img:require("../assets/cars/car1.png"),lng:113.529598,lat:23.188665,rtsp:"rtsp://238o86j398.qicp.vip:32943/live/av0",video:require("../assets/videos/1.mp4")},
        {licenseNum:"A8456",deviceNum:"LGW2_002",num:"0211",img:require("../assets/cars/car1.png"),lng:113.502074,lat:23.194047,rtsp:"rtsp://root:root@238o86j398.qicp.vip:50282/cam0_0",video:require("../assets/videos/2.mp4")}
      ],
      carMarkers:[],
      carVideo:false,
      carMsg:"",
      //
      hydrantStatus:false,
      // hydrants:[
      //   {lng:113.390007,lat:22.806769,img:require("../assets/hydrants/hydrant1.png")},
      //   {lng:113.388489,lat:22.80846,img:require("../assets/hydrants/hydrant2.png")}
      // ],
      hydrants:[],
      hydrantsMarkers:[],
      picture:false,
      pictureMsg:"",
      //
      smokeStatus:false,
      // smokes:[
      //   {lng:113.372282,lat:22.821729,img:require("../assets/smokes/smoke1.png")},
      //   {lng:113.435163,lat:22.823727,img:require("../assets/smokes/smoke2.png")}
      // ],
      smokes:[],
      smokeMarkers:[],
      //
      wsdStatus:false,
      wsd:[],
      wsdMarkers:[],
      //
      stationStatus:false,
      stations:[
        // {name:"",deviceNum:"LGW3_001",num:"0310",lng:113.434445,lat:22.79721,img:require("../assets/stations/station1.png"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        // {name:"",deviceNum:"LGW3_002",num:"0311",lng:113.407731,lat:22.80248,img:require("../assets/stations/station2.png"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
        {name:"",deviceNum:"LGW3_001",num:"0310",lng:113.522484,lat:23.166938,img:require("../assets/stations/station1.png"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        {name:"",deviceNum:"LGW3_002",num:"0311",lng:113.473975,lat:23.17232,img:require("../assets/stations/station2.png"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
      ],
      stationMarkers:[],
      stationVideo:false,
      stationMsg:"",
      // 
      miniStatus:false,
      miniStations:[
        // {name:"",deviceNum:"LGW4_001",num:"0410",lng:113.443618,lat:22.816563,img:require("../assets/stations/mini1.png"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        // {name:"",deviceNum:"LGW4_002",num:"0411",lng:113.452601,lat:22.810633,img:require("../assets/stations/mini2.png"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
        {name:"",deviceNum:"LGW4_001",num:"0410",lng:113.539803,lat:23.173383,img:require("../assets/stations/mini1.png"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        {name:"",deviceNum:"LGW4_002",num:"0411",lng:113.521765,lat:23.196106,img:require("../assets/stations/mini2.png"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
      ],
      miniMarkers:[],
      //
      roomStatus:false,
      rooms:[
        // {name:"",deviceNum:"LGW5_001",num:"0510",lng:113.379731,lat:22.829754,img:require("../assets/controlRoom/control1.jpg"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        // {name:"",deviceNum:"LGW5_002",num:"0511",lng:113.432192,lat:22.806769,img:require("../assets/controlRoom/control2.jpg"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
        {name:"",deviceNum:"LGW5_001",num:"0510",lng:113.466286,lat:23.194645,img:require("../assets/controlRoom/control1.jpg"),rtsp:"rtsp://admin:ABCabc123@192.168.1.64:554/Streaming/Channels/1"},
        {name:"",deviceNum:"LGW5_002",num:"0511",lng:113.54239,lat:23.198498,img:require("../assets/controlRoom/control2.jpg"),rtsp:"rtsp://admin:TseeTsee@192.168.1.65:554/Streaming/Channels/1"}
      ],
      roomMarkers:[],
      // 
      // message:[
      //   {unit:"",name:"",time:"09:14:37",type:"",result:"",src:require("../assets/fire.png")},
      //   {unit:"",name:"",time:"12:31:46",type:"",result:"",src:require("../assets/fire2.png")},
      //   {unit:"",name:"",time:"12:45:08",type:"",result:"",src:require("../assets/shan.jpg")},
