      // ],
      alarmMessage:[],
      alarms:[],
      dataShow:false,
      alarmStatus:false,
      alarmMsg:"",
      zIndex:10
    }
  },
  created(){
    this.$Axios.get(this.$url+"/fire/getFireDeviceInfo.do")
                .then((res)=>{
                  // if(window.console){
                  //   console.log(res);
                  // }
                  if(res.data.status){
                      let response=res.data.list;
                      for(let i=0;i<response.length;i++){
                        if(response[i].deviceTypeValue=="firehydrant"){
                          this.hydrants.push(response[i]);
                        }
                        if(response[i].deviceTypeValue=="md_smoke"){
                          this.smokes.push(response[i]);
                        }
                        if(response[i].deviceTypeValue=="weisidun"){
                          this.wsd.push(response[i]);
                        }
                      }
                  }
                }).catch((err)=>{
                  if(window.console){
                    console.log(err);
                  }
                })
    this.$Axios.get(this.$url+"/fire/getFireAlarmInfo.do")
                .then((res)=>{
                  // if(window.console){
                  //   console.log(res);
                  // }
                  if(res.data.status){
                     this.alarmMessage=res.data.list;
                  }
                }).catch((err)=>{
                  if(window.console){
                    console.log(err);
                  }
                })
    setInterval(()=>{
      this.$Axios.get(this.$url+"/fire/getFireAlarmRealTimeInfo.do",{params:{
                    t:Date.parse(new Date())
                  }})
                .then((res)=>{
                  // if(window.console){
                  //   console.log(res);
                  // }
                  if(res.data.status){
                      if(window.console){
                        console.log(res.data);
                      }
                      this.alarms.push(...res.data.list);
                      if(this.alarms.length>4){
                        this.alarms=this.alarms.slice(-4);
                      }
                  }
                }).catch((err)=>{
                  if(window.console){
                    console.log(err);
                  }
                })
    },1000)
  },
  mounted(){
    let map = new BMap.Map("mapshow");
    // let point = new BMap.Point(113.412588,22.809293); //
    let point = new BMap.Point(113.508614,23.182885); //
    map.centerAndZoom(point,15);
    map.enableScrollWheelZoom(true);
    map.setMapStyleV2({styleJson:styleJson});
    this.map=map;
    // this.showDaGang();
  },
  watch:{
    firemenStatus(newValue){
      let _this=this;
      if(newValue){
        for(let i=0;i<this.firemen.length;i++){
          let point = new BMap.Point(this.firemen[i].lng,this.firemen[i].lat);
          let myIcon = new BMap.Icon(require("../assets/fireman2.png"), new BMap.Size(28, 28));
          let marker = new BMap.Marker(point,{icon:myIcon});      
          this.map.addOverlay(marker);
          let position=null;
          let geoc = new BMap.Geocoder();  
          geoc.getLocation(point,(rs)=>{
              let addComp = rs.addressComponents;
              position=addComp.province + addComp.city + addComp.district + addComp.street + addComp.streetNumber;
          })
          marker.addEventListener("click",()=>{  
            // let sContent=``;
            // let infoWindow = new BMap.InfoWindow(sContent);  //   
            // _this.map.openInfoWindow(infoWindow,point); 
            let msg=`
                    <div style="width:30%;height:100%;float:left;background-color:#101213;padding:10px;">
                      <div style="height:30%;width:100%"><img src=${_this.firemen[i].img} height=100% class="img-circle center-block"></div>
                      <div style="height:50%;width:100%;padding:10px 15px;">
                        <div>${_this.firemen[i].deviceNum}</div>
                        <div>${_this.firemen[i].name}</div>
                        <div>${_this.firemen[i].num}</div>
                        <div>${position}</div>
                      </div>
                      <div style="height:20%;width:100%;padding:10px;">
                        <img src=${require("../assets/vioce1.png")} height=70% style="cursor: pointer;">
                        <img src=${require("../assets/micro.png")} height=70% style="padding-left:15%;cursor: pointer;">
                        <img src=${require("../assets/phone1.png")} height=70% style="padding-left:15%;cursor: pointer;">
                      </div>
                    </div>
                    <div style="width:70%;height:100%;float:left;background-color: #101213;">
                      <object type="application/x-vlc-plugin" id="vlc_1" events="True" codebase="axvlc.cab" style="height:100% !important;width:100% !important;">
                          <param name="mrl" value=${_this.firemen[i].video} />
                          <param name="volume" value="50" />
                          <param name="autoplay" value="true" />
                          <param name="loop" value="false" />
                          <param name="fullscreen" value="true" />
                      </object>
                    </div>`;
            _this.firemenMsg=msg;
            _this.firemenVideo=true;
          });   
          this.firemenMarkers.push(marker);
      }
      }else{
        for(let i=0;i<this.firemenMarkers.length;i++){
          this.map.removeOverlay(this.firemenMarkers[i]);
        }
        this.firemenMarkers=[];
      }
    },
    carStatus(newValue){
      let _this=this;
      if(newValue){
        for(let i=0;i<this.cars.length;i++){
          let point = new BMap.Point(this.cars[i].lng,this.cars[i].lat);
          let myIcon = new BMap.Icon(require("../assets/car2.png"), new BMap.Size(28, 28));
          let marker = new BMap.Marker(point,{icon:myIcon});      
          this.map.addOverlay(marker);
          let position=null;
          let geoc = new BMap.Geocoder();  
          geoc.getLocation(point,(rs)=>{
              let addComp = rs.addressComponents;
              position=addComp.province + addComp.city + addComp.district + addComp.street + addComp.streetNumber;
          })
          marker.addEventListener("click",()=>{  
            let msg=`
                    <div style="width:30%;height:100%;float:left;background-color:#101213;padding:10px;">
                      <div style="height:30%;width:100%"><img src=${_this.cars[i].img} height=100% class="img-circle center-block"></div>
                      <div style="height:50%;width:100%;padding:10px 15px;">
                        <div>${_this.cars[i].deviceNum}</div>
                        <div>${_this.cars[i].licenseNum}</div>
                        <div>${_this.cars[i].num}</div>
                        <div>${position}</div>
                      </div>
                      <div style="height:20%;width:100%;padding:10px;">
                        <img src=${require("../assets/vioce1.png")} height=70% style="cursor: pointer;">
                        <img src=${require("../assets/micro.png")} height=70% style="padding-left:15%;cursor: pointer;">
                        <img src=${require("../assets/phone1.png")} height=70% style="padding-left:15%;cursor: pointer;">
                      </div>
                    </div>
                    <div style="width:70%;height:100%;float:left;background-color: #101213;">
                      <object type="application/x-vlc-plugin" id="vlc_1" events="True" codebase="axvlc.cab" style="height:100% !important;width:100% !important;">
                          <param name="mrl" value=${_this.cars[i].video} />
                          <param name="volume" value="50" />
                          <param name="autoplay" value="true" />
                          <param name="loop" value="false" />
                          <param name="fullscreen" value="true" />
                      </object>
                    </div>`;
            _this.carMsg=msg;
