          this.map.removeOverlay(this.carMarkers[i]);
        }
        this.carMarkers=[];
      }
    },
    hydrantStatus(newValue){
      let _this=this;
      if(newValue){
        for(let i=0;i<this.hydrants.length;i++){
          let point = new BMap.Point(this.hydrants[i].pointY,this.hydrants[i].pointX);
          let myIcon = new BMap.Icon(require("../assets/hydrant2.png"), new BMap.Size(28, 28));
          let marker = new BMap.Marker(point,{icon:myIcon});      
          this.map.addOverlay(marker);
          marker.addEventListener("click",()=>{ 
            let msg=`
                    <div style="width:30%;height:100%;float:left;background-color:#101213;padding:10px;">
