    },
    roomStatus(newValue){
      let _this=this;
      if(newValue){
        for(let i=0;i<this.rooms.length;i++){
          let point = new BMap.Point(this.rooms[i].lng,this.rooms[i].lat);
          let myIcon = new BMap.Icon(require("../assets/room2.png"), new BMap.Size(28, 28));
          let marker = new BMap.Marker(point,{icon:myIcon});      
          this.map.addOverlay(marker);
