                      <object type="application/x-vlc-plugin" id="vlc_1" events="True" codebase="axvlc.cab" style="height:100% !important;width:100% !important;">
                          <param name="mrl" value=${_this.rooms[i].rtsp} />
                          <param name="volume" value="50" />
