        this.roomMarkers=[];
      }
    }
  },
  methods:{
    showData(){
      this.dataShow=!this.dataShow;
      this.alarmStatus=false;
    },
    showAlarm(src){
      this.alarmMsg=`<img src=${src}>`;
      this.alarmStatus=true;
    },
    move(event){
        let moveDiv=event.target.parentNode.parentNode;
        ++this.zIndex;
        moveDiv.style.zIndex=this.zIndex;
        let bodyWidth=$("body").width();
        let bodyHeight=$("body").height();
        $("body").append(`<div class="cover" style="top:0px;left:0px;position:absolute;z-index:${this.zIndex-1};width:${bodyWidth}px;height:${bodyHeight}px;opacity:0;"></div>`);   
        let a=event.pageX-$(moveDiv).offset().left;//
        let b=event.pageY-$(moveDiv).offset().top;//
        $(document).mousemove((e)=>{    
            let X=e.pageX-a;
            let Y=e.pageY-b;
            if(X<0){
                X=0;
            }else if(X>($(document).width()-$(moveDiv).width())){
                X=$(document).width()-$(moveDiv).width();
            }
            if(Y<0){
                Y=0;
            }else if(Y>($(document).height()-$(moveDiv).height())){
                Y=$(document).height()-$(moveDiv).height()-5;
            }
            $(moveDiv).css({"top":Y+"px","left":X+"px"});
        });
        $(document).mouseup((e)=>{
            $(document).unbind("mousemove");//         
            $("body").find(".cover").remove();  //                    
        });  
    },
    // 
    showDaGang(){
      let daGang=[
        new BMap.Point(113.424442,22.853411),new BMap.Point(113.422861,22.851812),new BMap.Point(113.421855,22.84675),new BMap.Point(113.420418,22.836891),
        new BMap.Point(113.423939,22.827698),new BMap.Point(113.420023,22.826866),new BMap.Point(113.415567,22.8251),new BMap.Point(113.412621,22.822235),
        new BMap.Point(113.407446,22.826133),new BMap.Point(113.404859,22.82097),new BMap.Point(113.405901,22.820637),new BMap.Point(113.398751,22.823335),
        new BMap.Point(113.364076,22.797317),new BMap.Point(113.366376,22.787721),new BMap.Point(113.382186,22.772526),new BMap.Point(113.415244,22.751198),
        new BMap.Point(113.446002,22.743732),new BMap.Point(113.483515,22.762162),new BMap.Point(113.497241,22.768428),new BMap.Point(113.487252,22.772826),
        new BMap.Point(113.475969,22.777758),new BMap.Point(113.478916,22.779624),new BMap.Point(113.495516,22.798016),new BMap.Point(113.476472,22.813341),
        new BMap.Point(113.448876,22.831595),new BMap.Point(113.425017,22.853577)
      ];
      for (let i = 0; i < daGang.length; i++) {
          let ply = new BMap.Polyline(daGang,{
            strokeWeight:5, 
            strokeColor: "#838080",
            strokeOpacity: 1,
            strokeStyle:"dashed"
          }); //
          this.map.addOverlay(ply);  //
      }
    },
    deleteThis(id){
      this.alarms=this.alarms.filter((elem)=>{
        return elem.id!=id;
      })
    }
  }
}
</script>
<style lang="less">
#fireControl{
  height:100%;
  width:100%;
  padding:0rem 15px 15px 15px;
  #myheader{
    height:60px;
    width:100%;
    color:white;
    font-weight: bold;
    img{
      padding-top:7px;
      padding-left:20px;
    }
    #text{
      font-size: 25px;
      color:#99d4ff;
      margin-top:12px;
    }
  }
  #main{
    height:calc(100% - 60px);
    width:100%;
    border:3px solid #345197;
    border-radius: 10px;
    #menu{
      height:100px;
      width:100%;
      padding:10px;
      background-color: #031830;
      padding:20px;
      #icons{
        height:100%;
        width: 100%;
        background-color: #012d4e;
        border:1px solid #023c68;
        padding:13px;
        .icon{
          margin:0px;
          margin-left: 1rem;
        }
        img{
          height: 100%;
          margin-left: 1rem;

        }
        span{
          color:white;      
          margin: 0px 5px 0px 10px;
        }
        #data{
          float:right;
          cursor: pointer;
          padding-top: 5px;
        }
      }
      
    }
    #map{
      height:calc(100% - 100px);
      width:100%;
      padding:1px;
      #mapshow{
        height: 100%;
        width:100%;
      }
    }
  }
  .videoShow{
    width: 600px;
    height: 350px;
    background-color: #192546;
    position:absolute; 
    z-index:10;
    .videoHide{
      width: 100%;
      height: 30px;
      .move{
        height: 100%;
        width: 95%;
        float: left;
        cursor:move;
      }
      span{
        float:right;
        margin-right: 10px;
        margin-top: 5px;
        cursor: pointer;
      }
    }
    .msg{
      width: 100%;
      height: calc(100% - 30px);
      padding:0px 15px 15px 15px;
    } 
    .iframe{
      width: 100%;
      height: 100%;
    } 
  }
  #firemen{
    top:163px;
    left:18px;
    color:white;
    object{
       width: 100% !important;
      height: 100% !important;
    }
  }
  #car{
    bottom:18px;
    left:18px;
    color:white;
    object{
      width: 100% !important;
      height: 100% !important;
    }
  }
  #station{
    bottom:18px;
    right:18px;
    color:white;
    object{
      width: 100% !important;
      height: 100% !important;
    }
  }
  #picture{
    top:163px;
    right:18px;
    color: white;
    object{
      width: 100% !important;
      height: 100% !important;
    }
  }
  #allData{
    width: 40%;
    height: 60px;
    position: absolute;
    top:calc(150px + 2%);
    left:calc(15px + 30%);
    z-index: 2;
    color:white;
    text-align: center;
    background-color: #031830;
    border: 2px solid white;
    padding: 15px;
    opacity: 0.8;
    .fireCell{
      width: 20%;
      height: 100%;
      float:left;
      border-right:2px solid white; 
    }
  }
  .title1{
    width: 150px;
    height: 30px;
    background: url("../assets/title4.png");
    background-size: 100% 100%;
    position: absolute;
    top:calc(150px + 15%);
    left:calc(15px + 10%);
    z-index: 2;
    color:white;
    text-align: center;
    padding-top: 5px;
    opacity: 0.8;
  }
  #dataWindow{
    width: 400px;
    height: 200px;
    padding:10px;
    position:absolute;
    // top:303px;
    top:calc(150px + 19%);
    // left:218px;
    left:calc(15px + 10%);
    z-index: 2;
    background-color: #031830;
    color:white;
    opacity: 0.8;
    border: 2px solid white;
    div{
      padding:0px;
      margin:0px;
    }
    .row{
      height: 30px;
    }
  }
  .title2{
    width: 150px;
    height: 30px;
    background: url("../assets/title4.png");
    background-size: 100% 100%;
    position: absolute;
    top:calc(150px + 15%);
    right:calc(15px + 10%);
    z-index: 2;
    color:white;
    text-align: center;
    padding-top: 5px;
    opacity: 0.8;
  }
  #alarm{
    width: 500px;
    height: 200px;
    padding:10px;
    position:absolute;
    top:calc(150px + 19%);
    right:calc(15px + 10%);
    z-index: 3;
    background-color: #031830;
    color:white;
    opacity: 0.8;
    border: 2px solid white;
    #tableHead{
      width: 100%;
      height: 30px;
      background-color: #023c68;
      color:rgb(2, 252, 196);
      .cell{
        width: 16.6%;
        height: 100%;
        float: left;
        padding-top:1.5%;
        text-align: center;
      }
    }
    #tableBody{
      width: 100%;
      height:calc(100% - 30px);
      overflow: auto;
      .cells{
        width: 100%;
        height: 40px;
        border-bottom: 1px solid white;
        .cell{
          width: 16.6%;
          height: 100%;
          float: left;
          padding-top:1.5%;
          text-align: center;
        }
      }
    }
  }
  #alarmPicture{
    z-index: 3;
    top:calc(150px + 20%);
    left:calc(15px + 31%);
  }
  .warn{
    // height: 300px;
    width: 400px;
    // background-color: #99d4ff;
    position:absolute;
    right: 18px;
    bottom:18px;
  }
}
.BMap_bubble_title{
    color:black;
    font-size:13px;
    font-weight: bold;
    text-align:left;
}
.BMap_pop div:nth-child(1){
    border-radius:7px 0 0 0;
}
.BMap_pop div:nth-child(3){
    border-radius:0 7px 0 0;background:#ABABAB;;
    /*background: #ABABAB;*/
    width:23px;
    width:0px;
    height:0px;
}
.BMap_pop div:nth-child(3) div{
    border-radius:7px;
}
.BMap_pop div:nth-child(5){
    border-radius:0 0 0 7px;
}
.BMap_pop div:nth-child(5) div{
    border-radius:7px;
}
.BMap_pop div:nth-child(7){
    border-radius:0 0 7px 0 ;
}
.BMap_pop div:nth-child(7){
    border-radius:0 0 7px 0 ;
}
.ivu-icon{
  cursor: pointer;
}
</style>





